import React, {useState} from 'react'
import {Button} from 'react-bootstrap'

function ReturnTopButton () {
    const scrollTop = () =>{
        window.scrollTo({top: 0, behavior: 'smooth'});
     }

    const [showScroll, setShowScroll] = useState(false)

    const checkScrollTop = () => {    
        if (!showScroll && window.pageYOffset > 800){
            setShowScroll(true)    
        } else if (showScroll && window.pageYOffset <= 800){
            setShowScroll(false)    
        }  
    }
    window.addEventListener('scroll', checkScrollTop)
    
    return (
        <Button onClick={scrollTop } 
        className='animate__animated animate__bounce animate__fast top-button'
        style={{display: showScroll ? 'flex' : 'none'}} 
        variant="dark">
        <img src="https://res.cloudinary.com/davis-cloud/image/upload/v1600058350/img_118804_gocss7.png" alt="not found"/> 
        </Button>
    )
}

export default ReturnTopButton